// View mode handlers
const handleViewChange = () => {
  if (viewMode.value === 'calendar') {
    // Load all interviews for current month when switching to calendar view
    const now = new Date();
    const startOfMonth = new Date(now.getFullYear(), now.getMonth(), 1);
